import React from 'react';
import Album from './Album';

const Albums = (prop) => {
  let {albums} = prop;
  let listOfAlbums = albums.map((album, index) => {
    return <Album data={album} key={index}></Album>
  })

  return (
    <div className="albums-container">
      {listOfAlbums}
    </div>
  )
}

export default Albums;