import React from 'react';
import '../scss/App.scss';
import albums from '../data/gorillaz';
import Albums from './Albums';

function App() {

  const gorillazAlbums = albums.results.albummatches.album;
  const arrayOfAlbums = gorillazAlbums.map((album) => album);

  return (
    <div className="App">
      <Albums albums={arrayOfAlbums}/>
    </div>
  );
}

export default App;
